
import React, { Component } from 'react';
import './post.css'
import EditorWrap from '../Editer/editorwrap';
import Media_img from '../Editer/Add_media';
import { toast } from 'react-toastify';
import {
  action_create_or_edit_post,
  get_posts,
  get_category_menu_list,
  clear_catche_by_id,
  remove_page_by_id
} from '../axios';
const offset=25;
export default class Posts extends Component {
  constructor(props) {
    super(props)
    this.state = {
      page:0,
      is_show_more:false,
      is_editer:false,
      triger_editer:0,
      is_open_media:false,
      img_ref:'',
      text_check:'',
      type_editer:{
        type:'',
        content:''
      },
      data_in:{
        is_open:false,
        type_editer:'html',
        des:'Chỉnh sửa nội dung'
      },
      list_page:[
        // {
        //   id:12,
        //   title:'Giường ngủ bằng sắt hộp Hoàng Gia cao cấp - Xả Kho Hàng tết 2023',
        //   thumnail:'https://anbinhnew.com/wp-content/uploads/2021/01/ban-hoc-mau-hong-nhua-dai-loan-day-1m.jpg',
        //   link:"#",
        //   status:'publish',
        //   short_des:'Bàn học đôi dành cho 2 bé ngồi học, bàn có liền kệ sách. Được làm bằng nhựa cao cấp, giá thành rẻ đang được ưa chuộng trên thị trường. Bàn học có màu xanh dương, màu hồng và màu trắng dành cho bé gái. Được sử dụng để học tập và làm việc. Kích thước ngang 1m4. Phù hợp với mọi lứa tuổi, học sinh tiểu học, mẫu giáo. Bộ bàn ghế học sinh có kệ sách bằng nhựa rất đẹp. Hiện tại, chúng tôi hỗ trợ sản phẩm bàn học đôi cho bé tra và gái tại HCM, Bình Dương, Thủ Đức, Gò Vấp, Tân Phú, Tân bình, Bình Thành, Dĩ An, Các Quận HCM.',
        //   long_des:'xxxx',
        //   parent_Category:2,
        // },
 
      ],
      selected_category_list:-1,
      list_category:[
        // {
        //   id:29,
        //   name:'Danh mục số 1'
        // } 
      ],
      selected_page:{
          parent_Category:0,
          id:-1,
          title:'',
          thumnail:'',
          link:"",
          status:'private',
          short_des:'',
          long_des:'',
          shop_adress:'',
          is_show_ads:true
      }
    }
  }
  async componentDidMount() {
    let text_check=await window.localStorage.getItem("selected");
    text_check=text_check==null?"":text_check;
    let a=await get_posts(0,-1);
    let b=await get_category_menu_list()
    console.log("🚀 ~ file: Posts.js:84 ~ Posts ~ componentDidMount ~ b", b)
    let is_show_more=false;
    if(a.length==offset) is_show_more=true;
    this.setState({text_check:text_check,list_page:a,is_show_more:is_show_more,list_category:b})
    // let API_telegram_og=await get_API_telegram();
    // let orders= await get_orders();
    // this.setState({
    //   API_telegram_og:API_telegram_og.API,
    //   orders:orders
    // })
  }

  render() {
    let {is_editer,text_check,triger_editer,type_editer,data_in,list_page,selected_page,is_open_media,list_category,selected_category_list,is_show_more,page}=this.state;
    return (
      <React.Fragment>
 
        {!is_editer&&<div className='container mt-50 mgbt'>
          <div className='row'>
            <div className='col-8'>
              <button type="button" className="btn btn-primary" 
                onClick={()=>{
                  let {list_category}=this.state;
                  window.is_editer=true;
                  this.setState({
                    is_editer:true,
                    selected_page:{
                      parent_Category:list_category.length>0?list_category[0].id:-1,
                      id:'-1',
                      title:'',
                      thumnail:'',
                      link:'',
                      status:'private',
                      short_des:'',
                      long_des:'',
                      shop_adress:'',
                      is_show_ads:true
                  }
                  })
                }}
              >
                <i className="fa-solid fa-plus"></i><span className="hgfs">Tạo mới</span>
              </button>
            </div>
            <div className='col-4'>
              <select className="form-select form-select-sm"
                value={selected_category_list}
                onChange={(e)=>{
                  this.setState({
                    selected_category_list:e.target.value,
                  })

                  get_posts(0,e.target.value).then((rs) => {
                    let is_show_more=false;
                    if(rs.length==offset) is_show_more=true;
                    this.setState({
                      is_show_more:is_show_more,
                      page:0,
                      list_page:rs,
                    })
                  })
                  // let is_show_more=false;
                  // if(a.length==offset) is_show_more=true;

                  // this.setState({
                  //   selected_category_list:e.target.value,
                  //   // is_show_more:is_show_more,
                  //   // page:0,
                  //   // list_page:a,
                  // })
                }}
              >
                <option value={-1} >Tất cả</option>
                {
                  list_category.map((e,i)=>{
                    return <option key={i} value={e.id} >{e.name}</option>
                  })
                }
              </select>
            </div>
          </div>
        </div>}
        {!is_editer&&<div className='container'>
          <table className="table table-hover table-striped">
            <thead>
              <tr>
                <th scope="col" style={{width:'1%'}}>#</th>
                <th scope="col" style={{width:'40%'}}>Tiêu đề Trang</th>
                <th scope="col" style={{width:'8%'}}>Trạng thái</th>
                <th scope="col" style={{width:'15%'}}>Điều chỉnh</th>
                <th scope="col" style={{width:'15%'}}>Cache</th>
              </tr>
            </thead>
            <tbody>
              
                  {
                    list_page.map((e,i)=>{
                      let chek=text_check.search(e.id+",")==-1?false:true;
                      let show_time='';
                      if(e.time_catche){
                        let time_his =(e.time_catche*1000);
                        let time_now= new Date().getTime();
                        let time_long=Math.floor((time_now-time_his)/60000);//all phut
                        let count_day=Math.floor(time_long/1440);//
                        let count_hour=Math.floor(time_long/60)-count_day*24;//
                        let count_Minutes=time_long-count_day*24*60-count_hour*60;
                        if(count_day>0){
                            show_time+=count_day+" ngày ";
                        }
                        if(count_hour>0){
                            show_time+=count_hour+" giờ ";
                        }
                        show_time+=count_Minutes+ " phút trước";
                      }
                      return (<tr className={chek?"checkeds":""}>
                                  <td style={{cursor:"pointer"}}
                                      onClick={()=>{
                                          let {text_check}=this.state;
                                          if(text_check.search(e.id+",")==-1){
                                            text_check+=e.id+",";
                                          }else{
                                            text_check=text_check.replace(e.id+",", "");
                                          }
                                          this.setState({text_check:text_check});
                                          window.localStorage.setItem("selected",text_check);
                                        }}
                                  >
                                    <input type="checkbox"
                                      checked={chek}
                                      onChange={()=>{
                                        let {text_check}=this.state;
                                        if(text_check.search(e.id+",")==-1){
                                          text_check+=e.id+",";
                                        }else{
                                          text_check=text_check.replace(e.id+",", "");
                                        }
                                        this.setState({text_check:text_check});
                                        window.localStorage.setItem("selected",text_check);
                                      }}
                                    />
                                  </td>
                                  <td>
                                    <div className='oiu' key={e.id}>
                                      <div className='wlo'>
                                        <img src={e.thumnail} className='imgxd'/>
                                      </div>
                                      <div className='wro'>
                                        <a target="_blank" href={e.link} className='afred'>{e.title}</a>
                                      </div>
                                    </div>
                                  </td>
                                  <td>
                                    {e.status=="publish"&&<span className="publish">Công khai</span>}
                                    {e.status=="private"&&<span className="private">Riêng tư</span>}
                                  </td>
                                  <td>
                                   <div className='controlh'>
                                      <div className='delete iconh'
                                        onClick={async()=>{
                                          if(window.confirm(`Xác nhận xóa bài viết: "${e.title}" (${e.id})`)){
                                            let a=await remove_page_by_id(e.id);
                                            if(a){
                                              toast.success(`Xóa thành công.`,{theme: "colored"})
                                              let {list_page}=this.state;
                                              list_page.splice(i,1);
                                              this.setState({list_page:list_page})
                                            }else{
                                              toast.error('Lỗi rồi bạn ơi!.',{theme: "colored"})
                                            }
                                          }
                                        }}
                                      ><i className="fa-solid fa-trash-can"></i><span>Xóa</span></div>
                
                                      <div className='edit iconh'
                                        onClick={()=>{
                                          window.is_editer=true;
                                          this.setState({
                                            is_editer:true,
                                            selected_page:{
                                              id:e.id,
                                              title:e.title,
                                              thumnail:e.thumnail,
                                              link:e.link,
                                              status:e.status,
                                              short_des:e.short_des,
                                              long_des:e.long_des,
                                              parent_Category:e.parent_Category,
                                              shop_adress:e.shop_adress==undefined?"":e.shop_adress,
                                              is_show_ads:e.is_show_ads==undefined?true:e.shop_adress,
                                          }
                                          })
                                        }}
                                      ><i className="fa-solid fa-pen-to-square"></i><span>Edit</span></div>
                                    </div>
                                  </td>
                                  <td style={{fontSize:"12px"}} >
                                      <div>
                                        <span>{show_time}</span><br/>
                                        {show_time!=""&&<span className='cleacs'
                                          onClick={async()=>{
                                            if(window.confirm(`Xác nhận xóa catche bài viết : ${e.title}`)){
                                              let a =await clear_catche_by_id({
                                                id:e.id,
                                                type:'post'
                                              })
                                              if(a.status){
                                                toast.success(`Xóa CATCHE thành công.`,{theme: "colored"})
                                                let {list_page}=this.state;
                                                list_page[i].time_catche='';
                                                this.setState({list_page:list_page})
                                              }else{
                                                toast.error('Lỗi rồi bạn ơi!.',{theme: "colored"})
                                              }
                                            }
                                          }}
                                        >Clear Cache</span>}
                                      </div>
                                    </td>
                              </tr>)
                    })
                  }
                   
                
    
            </tbody>
          </table>
          <div style={{textAlign:"center"}}>
            {is_show_more&&<button className='butonxz'
              onClick={async()=>{
                let a=await get_posts(page+1,selected_category_list);
                let is_show_more=false;
                if(a.length==offset) is_show_more=true;
                this.setState({
                  is_show_more:is_show_more,
                  list_page:[...list_page,...a],
                  page:page+1
                })
              }}
            >Xem thêm</button>}
          </div>
        </div>}
        {/*  */}
        {is_editer&&<div className='container'>
          <div className='row wrap-s'>
            <div className='col-3'>
              Lựa chọn danh mục:
              <div style={{marginTop:"5px"}}>
                <select className="form-select form-select-sm"
                  value={selected_page.parent_Category}
                  onChange={(e)=>{
                    let {selected_page}=this.state;
                    selected_page.parent_Category=e.target.value;
                    this.setState({selected_page:selected_page})
                  }}
                >
                {
                  list_category.map((e,i)=>{
                    return <option key={i} value={e.id} >{e.name}</option>
                  })
                }
               
                </select>
              </div>
            </div>
            <div className='col-3'>
              <span style={{color:"red"}}>Công Khai</span> hay <span style={{color:"red"}}>Riêng Tư</span> bài viết này:
              <div style={{marginTop:"5px"}}>
                <select className="form-select form-select-sm" 
                  value={selected_page.status}
                  onChange={(e) => {
                      let {selected_page}=this.state;
                      selected_page.status=e.target.value
                      this.setState({selected_page:selected_page})
                  }}
                >
                  <option  value="private"  
                  >Riêng tư</option>
                  <option  value="publish" 
                  >Công khai</option>
                </select>
              </div>
            </div>
            <div className='col-3'>
              Lựa chọn hình đại diện cho bài viết: 
              <div style={{position:"relative"}}>
                <button className='buzz' 
                  onClick={()=>{
                    this.setState({
                      is_open_media:true,
                      img_ref:'thumnail'
                    })
                  }}
                >
                  <i className="fa-solid fa-photo-film"></i> <span>Add Media</span>
                </button>
                <img src={selected_page.thumnail}className='thumnailz'/>
              </div>
            </div>
            <div className='col-3'>
              <div className="form-check form-switch">
                <input className="form-check-input" type="checkbox" id="flexSwitchCheckDefault"
                  checked={selected_page.is_show_ads}
                  onChange={(e)=>{
                    let {selected_page}=this.state;
                    selected_page.is_show_ads=!selected_page.is_show_ads;
                    this.setState({selected_page:selected_page})
                  }}
                />
                <label className="form-check-label" for="flexSwitchCheckDefault">Show ads
                  ({!selected_page.is_show_ads&&<strong style={{color:'rosybrown'}}>Tắt</strong>}
                  {selected_page.is_show_ads&&<strong style={{color:'green'}}>Bật</strong>})
                </label>
              </div>
            </div>
          </div>
          <div className='row wrap-s'>
              <div className="input-group mb-3">
                <span className="input-group-text">Tiêu đề:</span>
                <input type="text" className="form-control" placeholder="..." aria-label="Username" aria-describedby="basic-addon1" 
                  value={selected_page.title}
                  onChange={(e)=>{
                    let {selected_page}=this.state;
                    selected_page.title=e.target.value;
                    this.setState({selected_page:selected_page})
                  }}
                  
                />
              </div>
              <div className="form-floating">
                <textarea   className="form-control" style={{height: '150px', fontSize:"14px"}}
                  value={selected_page.short_des}
                  onChange={(e)=>{
                    let {selected_page}=this.state;
                    selected_page.short_des=e.target.value;
                    this.setState({selected_page:selected_page})
                  }}
                ></textarea>
                <label >Mô tả ngắn :</label>
              </div>
          </div>
          <div className='row wrap-s' style={{position:"relative"}}>
            <span>Mô tả dài:</span>
            <button type="button" className="btn-clipboard"  
              onClick={()=>{
                this.setState({
                  triger_editer:triger_editer+1,
                  type_editer:{
                    type:'long_des',
                    content:selected_page.long_des
                  },
                  data_in:{
                    is_open:true,
                    type_editer:'html',
                    des:'Chỉnh sửa nội dung'
                  },
                })
              }}
            >Chỉnh sửa</button>
            <div className="sty">
                <section className="contents">
                  <div  dangerouslySetInnerHTML={{__html: selected_page.long_des}}>

                  </div>
                </section>
            </div>
          </div>
          <div className='row wrap-s' style={{position:"relative"}}>
            <span>shop_adress:</span>
            <button type="button" className="btn-clipboard"  
              onClick={()=>{
                this.setState({
                  triger_editer:triger_editer+1,
                  type_editer:{
                    type:'shop_adress',
                    content:selected_page.shop_adress
                  },
                  data_in:{
                    is_open:true,
                    type_editer:'html',
                    des:'Chỉnh sửa nội dung: danh sách các địa chỉ cửa hàng'
                  },
                })
              }}
            >Chỉnh sửa</button>
            <div className="sty">
                <section className="contents">
                  <div  dangerouslySetInnerHTML={{__html: selected_page.shop_adress}}>

                  </div>
                </section>
            </div>
          </div>
        </div>}
        {is_editer&&<EditorWrap
          triger={triger_editer} 
          content={type_editer.content}
          data_in={data_in}
          fs_close={()=>{
            this.setState({data_in:{is_open:false, type_editer:'', des:''}})
          }}
          fsReturn_editer={(long_des)=>{
            let {selected_page,type_editer}=this.state;
            if(type_editer.type=="long_des"){
              selected_page.long_des=long_des
            }else if(type_editer.type=="shop_adress"){
              selected_page.shop_adress=long_des
            }
            this.setState({selected_page:selected_page})
          }}
        />}
          {is_editer&&<div className="footer-edit">
          <button type="button" className="btn btn-secondary" 
            onClick={()=>{
              window.is_editer=false;
              this.setState({
                is_editer:false
              })
            }}
          >Hủy</button>

          <button type="button" className="btn btn-primary"
            onClick={async()=>{
              let {list_page}=this.state;
              if(selected_page.title.length>8){
                let rs={
                  parent_Category:selected_page.parent_Category,
                  id:selected_page.id,
                  title:selected_page.title,
                  thumnail:selected_page.thumnail,
                  status:selected_page.status,
                  short_des:selected_page.short_des,
                  long_des:selected_page.long_des,
                  shop_adress:selected_page.shop_adress,
                  is_show_ads:selected_page.is_show_ads,
                }
                let a=await action_create_or_edit_post({
                  title:rs.title,
                  id:rs.id,
                  status:rs.status,
                  parent_Category:rs.parent_Category,
                  metaA:JSON.stringify(rs)
                })
                if(a.id!=undefined){
                  let index=-1;
                  list_page.forEach((e,i) => {
                    if(e.id==a.id) index=i;  
                  });
                  if(index!=-1){
                    list_page[index]=a
                    toast.success(`Cập nhật thành công.`,{theme: "colored"})
                  }else{
                    list_page.unshift(a);
                    toast.success(`Tạo mới thành công.`,{theme: "colored"})
                  }
                  window.is_editer=false;
                  this.setState({list_page:list_page,is_editer:false})
                }else{
                  toast.error('Lỗi rồi bạn ơi!.',{theme: "colored"})
                }
              }else{
                toast.error('Tiêu đề thiếu hoặc quá ngắn!.',{theme: "colored"})
              }
            }}
          >{selected_page.id==-1?"Tạo mới bài viết":"Cập nhật bài viết"}</button>
          </div>}
          <Media_img 
          is_open_media={is_open_media}
          handleClose={()=>this.setState({is_open_media:false})}
          fsReturn_media={(img_return)=>{
            let {img_ref,selected_page}=this.state;
            switch(img_ref){
              case 'thumnail':
                selected_page.thumnail=img_return.img;
                this.setState({selected_page:selected_page})
                break;
            }         
          }}
        />
      </React.Fragment>
    );
  }
}

